@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap");

body {
  margin: 0;
  padding: 0;
  color: #3c484e;
  font-family: "Open Sans", sans-serif;
}

.Main {
  padding: 10px;
  grid-template-columns: minmax(auto, 768px);
  display: grid;
  justify-content: center;
}
#app {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
}

.Items {
    display: grid;
    grid-gap: 1.5rem;
    grid-row-gap: 1.5em;
    grid-template-columns: repeat( auto-fill, 200px);
    max-width: 90%;
}
 
.Card {
  width: 200px;
  text-decoration: none;
  box-shadow: 8px 14px 38px rgba(39, 44, 49, 0.06),
    1px 3px 8px rgba(39, 44, 49, 0.03);
  border-radius: 5px;
  margin: 0 0 20px 0;
  animation-duration: 4s;
  animation-name: fade;
  height: 350px;
}

.img-container {
  display: flex;
  overflow-x: hidden;

}

.Card:hover .img-container {
    display: flex;
    overflow-x: auto;
    scroll-snap-points-x: repeat(150px);
    scroll-snap-type: x mandatory;
  }
.visible {
    display: none;
    font-size: 12px;
    padding: 5px 10px;
}
.Card:hover .visible {
    display: block;
}
.Card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-inline-end: 8px;
  scroll-snap-align: center;
  padding: 2px;
}

.Card h2 {
  font-size: 12px;
  font-weight: 300;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
}
small {
    font-weight: bold;
    padding: 5px 10px;
}
small span {
    font-size: 10px;
    color: #24b504;
    font-weight: bold;
    
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}